*{
	box-sizing: border-box;
}
html, body{
	margin: 0;
		padding: 0;
}
body{
	font-weight: normal;
	font-size: 15px;
	min-width: 320px;
	position: relative;
	line-height: 1.6;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
}
footer{
	display: none;	
	padding-top: 45px;
	height: 638.5px;
	text-align: center;

	background-color: #007AFF;

	img{
		max-width: 100%;
	}
}
// ----------- main-screen---------------
@import "../fonts";
header{

	height: 125px;
	top: 0;
	left: 0;
	background-color: pink;
	opacity: .5;
	position: fixed;
	width: 100%;
	z-index: 1;

}
.main-screen{
	position: relative;
	height: 100vh;
	width: 100%;
	// text-align: center;
	/*   position: relative; */
	background: url(../img/latest-desktop.jpg) center center no-repeat;
	-webkit-background-size: cover;
	background-size: cover;
}
.main-screen-mark{
	position: absolute;
	width: 40px;
	height: 40px;
	border-radius: 50%;
	// left: 409px;
	// top: 418px;
	border: 3px solid rgba(0, 135, 223, 0.5);

	&__inner{
		border-radius: 50%;
		@include absolute-center;
		width: 35.29px;
		height: 35.29px;
		border: 2px solid rgba(0, 135, 223, 0.7);

	}
	&__icon{
		border-radius: 50%;
		@include absolute-center;
		width: 29.41px;
		height: 29.41px;
		border: 2px solid #FFFFFF;
		background-color: #0087DF;
		// background: #0087DF url(../img/main-scren-icons/ico_plus.svg) center center no-repeat;
		// background-size: 50%;
		img{
			@include absolute-center;
		}
	}
	
	&__info{
		min-width: 152px;
		position: absolute;
		bottom: calc(100% + 5px);
		left: -9px;
		padding: 9px 15px 12px 32px;
		background: #FFFFFF;
		border: 2px solid #B0C2D8;
		border-radius: 20px;
		transition: all .2s;
		transform: translateY(100%) scaleY(0);
		opacity: 0;

		p{
			margin-top: 0;
			margin-bottom: 0;
			font-family: AvenirBold;
			font-style: normal;
			font-weight: bold;
			font-size: 20px;
			line-height: 25px;

			color: #000000;
		}
		a{
			// margin-left: -5px;
			font-family: AvenirRegular;
			font-weight: normal;
			font-size: 12px;
			line-height: 15px;
			text-decoration-line: underline;
			color: #989595;
		}
		&:before{
			content: '';
			position: absolute;
			width: 22.02px;
			height: 19.41px;
			bottom: -3px;
			left: 10px;
			background-color: #fff;
			border-right: 2px solid #C4C4C4; 
			transform: matrix(0.71, 1.14, -0.44, 0.71, 0, 0);
		}


	}

	&:hover, &:active{
		z-index: 2;	
		background-color: #FAD746;
		background-image: url(../img/main-scren-icons/ico_dots-family.svg);
		background-position: center center;
		background-repeat: no-repeat;
		border-color: #fff;

		.main-screen-mark__inner{
			display: none;
		}
		.main-screen-mark__info{
			transform: translateY(0) scaleY(1);
			opacity: 1;
		}

	}
	

	&:first-child{
			top: 48%;
			left: 21%;
	}
	&:nth-child(2){
		top: 57%;
		left: 15%;
	}
	&:nth-child(3){
		top: 71%;
		left: 14%;
	}
	&:nth-child(4){
		top: 92%;
		left: 22%;
	}
	&:nth-child(5){
		top: 57%;
		left: 32%;
	}
	&:nth-child(6){
		top: 84%;
		left: 41%;
	}
	&:nth-child(7){
		top: 65%;
		left: 47%;
	}
	&:nth-child(8){
		top: 45%;
		left: 53%;
	}
	&:nth-child(9){
		top: 51%;
		left: 67%;
	}
	&:nth-child(10){
		top: 77%;
		left: 66%;
	}
}

