@media only screen and (max-width: 768px) {
	footer{
		display: block;	
	}
	header{
		height: 110px;
	}
	.main-screen{
		margin-top: -230px;
		// height: calc(100vh - 130px);
		background-image: url(../img/latest-mobile.jpg);
	}
	.main-screen-mark{
		// width: 20px;
		// height: 20px;
		// border: none;
		// background-color: rgba(0, 135, 223, 0.5);
		&__inner{
			// width: 17.65px;
			// height: 17.65px;
			// border: 2px solid rgba(0, 135, 223, 0.7);
		}
		&__icon{
			// width: 14.71px;
			// height: 14.71px;
			// border-width: 1px;

			img{
				// display: block;
				// max-width: 5px;
				// max-height: 5px;
			}
		}
		&__info{
			min-width: 101px;
			max-width: 150px;
			padding-left: 20px;	
			padding-bottom: 2px;
			padding-right: 10px;
			padding-top: 4px;
			border-radius: 10px;
			border-right-width: 1px;
			bottom: calc(100% + 10px);
			
			p{
				font-size: 14px;
				line-height: 17px;
			}
			a{
				position: relative;
				top: -8px;
				font-size: 10px;
				line-height: 12px;
			}
		}

		&:hover, &:active{
			border: 3px solid #fff;
			background-size: 65% auto;
		}

		&:first-child{
			top: 63%;
			left: 15%;
		}
		&:nth-child(2){
		top: 65%;
		left: 4%;
		}
		&:nth-child(3){
			top: 73%;
			left: 15%;
		}
		&:nth-child(4){
			top: 81%;
			left: 35%;
		}
		&:nth-child(5){
			top: 67%;
			left: 30%;
		}
		&:nth-child(6){
			top: 79%;
			left: 51%;
		}
		&:nth-child(7){
			top: 71%;
			left: 54%;
		}
		&:nth-child(8){
			top: 64%;
			left: 63%;
		}
		&:nth-child(9){
			top: 65%;
			left: 80%;
			.main-screen-mark__info{
				left: auto;	
				right: 0;	
				&:before{
					left: auto;	
					right: 10px;	
				}
			}
		}
		&:nth-child(10){
			top: 77%;
			left: 80%;
			.main-screen-mark__info{
				left: auto;	
				right: 0;	
				&:before{
					left: auto;	
					right: 10px;	
				}
			}
		}
		
	}

}
@media only screen and (max-width: 375px) {
	.main-screen{
		margin-top: -130px;	
	}
	.main-screen-mark{
		width: 20px;
		height: 20px;
		border: none;
		background-color: rgba(0, 135, 223, 0.5);
		&__info{
			left: -20px;
		}
		&__inner{
			width: 17.65px;
			height: 17.65px;
			border: 2px solid rgba(0, 135, 223, 0.7);
		}
		&__icon{
			width: 14.71px;
			height: 14.71px;
			border-width: 1px;

			img{
				display: block;
				max-width: 5px;
				max-height: 5px;
			}
		}
		&:nth-child(10),
		&:nth-child(9){
			.main-screen-mark__info{
				right: -12px;	
			}
		}
	}
}
// ----------- END main-screen---------------
